<template>
<div class="bj">
  <h1 class="center">书籍详情</h1>
  <h4 class="center">书籍名称：{{ book.bookName }}</h4>
  <h4 class="center">书籍类型：{{ book.bookType.typeName }}</h4>
  <h4 class="center">出版社：{{ book.publisher }}</h4>
  <h4 class="center">出版时间：{{ book.publishtime }}</h4>
  <img style="margin-left: 45%;width:100px;height:150px;" :src="`http://localhost:8080${ book.bookPhoto }`"> 
  <h4 class="center">作者姓名：{{ book.author.authorName }}</h4>
  <img style="margin-left: 45%;width:100px;height:150px;" :src="`http://localhost:8080${ book.author.authorPhoto  }`">
  <h4>作者简介：{{ book.author.authorInfo }}</h4>
</div>
</template>

<script>
import axios from 'axios'
import moment from "moment"

export default {
  props: {
    bookId: {
    }
  },
  data() {
    return {
      book: {
        bookType: {},
        author: {}
      }
    };
  },
  methods: {
    //获取数据
    fetch() {
      var that = this;
      axios
        .post("http://localhost:8080/Manager/getBookById?id=" + that.bookId)
        .then(function(response) {
          that.book = response.data.map.book;
          that.book.publishtime = moment(that.book.publishtime).format("YYYY-MM-DD")
        });
    }
  },
  created() {
    this.fetch();
  }
};
</script>

<style scoped>
.bj {
  background: url('../assets/img/bj.jpg') no-repeat center center;
  background-size: 100% 100%;
}
.center {
  text-align: center;
  font-family: serif;
}
</style>