import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import BorrowList from '../views/BorrowList.vue'
import BookList from '../views/BookList.vue'
import BookReturn from '../views/BookReturn.vue'
import BookInfo from '../views/BookInfo.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'main',
    component: Main,
    children: [
      { path: '/borrow/list', component: BorrowList },
      { path: '/book/list', component: BookList },
      { path: '/book/return/:broId', component: BookReturn, props: true },
      { path: '/book/info/:bookId', component: BookInfo, props: true }
    ]
  },
]

const router = new VueRouter({
  routes
})

const originalPush = VueRouter.prototype.push
   VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default router
