<template>
  <div class="bj">
    <h2 class="center">订单详情</h2>
    <h1 class="center">书名：   {{ model.book.bookName }}</h1>
    <h1 class="center">借阅用户：   {{ model.account.account }}</h1>
    <h1 class="center">申请时间：   {{ model.applytime }}</h1>
    <h1 class="center">开始借阅时间：   {{ model.lendtime }}</h1>
    <h1 class="center">归还时间：   {{ model.returntime }}</h1>
    <img :src="`http://localhost:8080${model.book.bookPhoto}`" style="width: 100px;height: 150px;margin-left:45%;">
    <br/>
    <el-button style="margin-left:45%"  type="primary" size="small" @click="submit">确认还书</el-button>
  </div>
</template>

<script>
import Axios from "axios";
import moment from "moment"

export default {
  props: {
    broId: {}
  },
  data() {
    return {
      bro: {},
      model: {
        book: {
        },
        account: {}
      }
    }
  },
  methods: {
    //获取数据
    fetch() {
      var that = this;
      Axios.post(
        "http://localhost:8080/User/returnBook?broId=" + that.broId
      ).then(function(response) {
        that.model = response.data.map.brrowinfoByBroId;
        var usetime = (that.model.returntime - that.model.lendtime)/(24*60*60*1000)
        that.model.lendtime = moment(that.model.lendtime).format("YYYY-MM-DD")
        that.model.returntime = moment(that.model.returntime).format("YYYY-MM-DD")
        if (usetime > that.model.applytime) {
          alert("使用时间超出申请时间，下次借阅请注意")
        } else {
          that.$message({
              type: "success",
              message: "按时归还，请继续保持"
            });
        }
        that.model.status = 1
      });
    },
    //提交还书操作
    submit() {
      var that = this
      that.bro.bookId = that.model.book.bookId
      that.bro.bookNumber = that.model.book.bookNumber
      that.bro.broId = that.model.broId
      that.bro.status = that.model.status
      Axios.post("http://localhost:8080/User/submitBrrow", this.$qs.stringify(that.bro))
      .then(function(response) {
        console.log(response)
        if (response.data.code == 100) {
          that.$router.push('/borrow/list')
          that.$message({
              type: "success",
              message: "归还成功"
            });      
        }
      })
    }
  },
  created() {
    this.fetch()
  }
};
</script>

<style scoped>
.bj {
  background: url('../assets/img/bj.jpg') no-repeat center center;
  background-size: 100% 100%;
}
.center {
  text-align: center;
  font-family: serif;
}
</style>