<template>
  <div>
    <h1>书籍列表</h1>
    <el-input
      type="text"
      style="width: 200px;"
      placeholder="请输入要查询的书名"
      v-model="bookName.bookName"
      @keyup.enter.native="searchName"
    ></el-input>
    <el-button type="primary" size="small" style="margin-left: 1rem;" @click="searchName">根据书名查询</el-button>
    <el-input
      type="text"
      style="width: 200px;margin-left: 1rem;"
      placeholder="请输入作者全名"
      v-model="authorName.authorName"
      @keyup.enter.native="searchAuthor"
    ></el-input>
    <el-button type="primary" size="small" style="margin-left: 1rem;" @click="searchAuthor">根据作者查询</el-button>
    <el-select style="margin-left: 1rem;" v-model="book.id" @keyup.enter.native="searchType">
      <el-option
        v-for="item of bookType"
        :key="item.typeId"
        :label="item.typeName"
        :value="item.typeId"
      ></el-option>
    </el-select>
    <el-button type="primary" size="small" style="margin-left: 1rem;" @click="searchType">根据类型查询</el-button>
    <el-table :data="model.list">
      <el-table-column prop="bookId" label="ID" width="120"></el-table-column>
      <el-table-column prop="bookName" label="书籍名称" width="120"></el-table-column>
      <el-table-column prop="bookPhoto" label="图片" width="160">
        <template slot-scope="scope">
          <img
            style="width:60px;height: 60px;"
            :src="`http://localhost:8080${scope.row.bookPhoto}`"
          />
        </template>
      </el-table-column>
      <el-table-column prop="bookNumber" label="数量" width="120"></el-table-column>
      <el-table-column prop="author.authorName" label="作者" width="120"></el-table-column>
      <el-table-column prop="publisher" label="出版社" width="120"></el-table-column>
      <el-table-column label="操作" width="350">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/book/info/${scope.row.bookId}`)"
          >详情</el-button>
          <el-input
            type="number"
            style="width: 180px;margin-left: 1rem;"
            placeholder="请输入申请时间（天）"
            v-model="scope.row.applytime"
          ></el-input>
          <el-button
            type="primary"
            style="margin-left: 1rem;"
            size="small"
            @click="borrowBook(scope.row)"
          >借书</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin-top: 1rem;margin-left: 20rem"
      background
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      @prev-click="pre"
      @next-click="next"
      :page-size="model.pageSize"
      :total="model.total"
    ></el-pagination>
  </div>
</template>

<script>
import Axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      bookName: {},
      authorName: {},
      model: {},
      page: { pn: 0 },
      bookType: [],
      book: {},
      bro: {}
    };
  },
  methods: {
    // 获取表格数据
    fetch() {
      var that = this;
      var pn = this.page.pn + 1;
      Axios.post("http://localhost:8080/Book/getBooks?pn=" + pn).then(function(
        response
      ) {
        that.model = response.data.map.page;
      });
    },
    // 分页的相关方法
    pre() {
      this.page.pn -= 1;
      this.fetch();
    },
    next() {
      this.page.pn += 1;
      this.fetch();
    },
    handleCurrentChange(val) {
      this.page.pn = val - 1;
      this.fetch();
    },
    //时间格式转化
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD");
    },
    //书名模糊查询
    searchName() {
      if (this.bookName.bookName == "") {
        this.$router.go(0);
      } else {
        var that = this;
        Axios.post(
          "http://localhost:8080/Book/getBookByName",
          this.$qs.stringify(that.bookName)
        ).then(function(response) {
          if (response.data.code == 100) {
            that.model.list = response.data.map.bookByName;
            that.model.pageSize = 100;
            // that.model.total = response.data.map.bookByName.length;
          } else {
            that.$message({
              type: "error",
              message: response.data.map.err_msg
            });
          }
        });
      }
    },
    //作者全名查询
    searchAuthor() {
      if (this.authorName.authorName == "") {
        this.$router.go(0);
      } else {
        var that = this;
        Axios.post(
          "http://localhost:8080/Book/getBookByAuthor",
          this.$qs.stringify(that.authorName)
        ).then(function(response) {
          if (response.data.code == 100) {
            that.model.list = response.data.map.bookByAuthor;
            that.model.pageSize = 100;
            // that.model.total = response.data.map.bookByAuthor.length;
          } else {
            that.$message({
              type: "error",
              message: response.data.map.err_msg
            });
          }
        });
      }
    },
    //获取书籍类型
    getBookType() {
      var that = this;
      Axios.post("http://localhost:8080/Book/getBookType").then(function(
        response
      ) {
        that.bookType = response.data.map.booktype;
      });
    },
    //根据类型查询
    searchType() {
      if (this.book.id == "") {
        this.$router.go(0);
      } else {
        var that = this;
        Axios.post(
          "http://localhost:8080/Book/getBookByType",
          this.$qs.stringify(that.book)
        ).then(function(response) {
          if (response.data.code == 100) {
            that.model.list = response.data.map.bookByType;
            that.model.pageSize = 100;
            // that.model.total = response.data.map.bookByType.length;
          } else {
            that.$message({
              type: "error",
              message: response.data.map.err_msg
            });
          }
        });
      }
    },
    // 借书按钮
    borrowBook(row) {
      var that = this;
      that
        .$confirm(
          `你将借阅"${row.bookName}"，申请时间为"${row.applytime}"，是否继续?`,
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
        .then(() => {
          that.bro.bookId = row.bookId;
          that.bro.bookNumber = row.bookNumber;
          that.bro.bookid = row.bookId;
          that.bro.applytime = row.applytime;
          if (that.bro.applytime == undefined || that.bro.applytime < 0) {
            that.$message({
              type: "error",
              message: "申请时间不能小于等于零"
            });
          } else {
            Axios.post(
              "http://localhost:8080/User/brrowBook",
              this.$qs.stringify(that.bro)
            ).then(function(response) {
              if (response.data.code == 100) {
                that.$message({
                  type: "success",
                  message: "借阅成功"
                });
                window.location.reload();
              } else {
                that.$message({
                  type: "error",
                  message: response.data.map.err_msg
                });
              }
            });
          }
        });
    }
  },
  created() {
    this.fetch();
    this.getBookType();
  }
};
</script>