<template>
  <el-table :data="borrow.brrowInfo">
    <el-table-column prop="broId" label="订单号" width="120"></el-table-column>
    <el-table-column prop="book.bookName" label="书籍" width="120"></el-table-column>
    <el-table-column prop="book.bookPhoto" label="图片" width="160">
      <template slot-scope="scope">
        <img
          style="width:60px;height: 60px;"
          :src="`http://localhost:8080${scope.row.book.bookPhoto}`"
        />
      </template>
    </el-table-column>
    <el-table-column prop="lendtime" :formatter="dateFormat" label="借阅时间" width="120"></el-table-column>
    <el-table-column prop="applytime" label="申请时间" width="120"></el-table-column>
    <el-table-column prop="status" :formatter="statusFormat" label="状态" width="120"></el-table-column>
    <el-table-column label="操作" width="220">
      <template slot-scope="scope">
        <el-button v-if="scope.row.status == 0" type="primary" size="small" @click="$router.push(`/book/return/${scope.row.broId}`)">还书</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import Axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      borrow: {}
    };
  },
  methods: {
    //获取数据
    fetch() {
      var that = this;
      Axios.post("http://localhost:8080/User/getBrrowInfo").then(function(
        response
      ) {
        that.borrow = response.data.map;
      });
    },
    //时间格式转化
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD");
    },
    //状态格式转化
    statusFormat(row, column) {
      var status = row[column.property];
      if (status == 0) {
        return "未归还";
      } else {
        return "已归还";
      }
    },
  },
  created() {
    this.fetch();
  }
};
</script>